.blob1 {
  z-index: -1;
  overflow: hidden;
  position: absolute;
  overflow: hidden;
  right: 0px;
  top: 0px;
  height: 90%;
  animation: animation1 18s linear infinite;
}

.blob2 {
  z-index: -1;
  overflow: hidden;
  position: absolute;
  overflow: hidden;
  right: -250px;
  top: -100px;
  height: 80%;
  animation: animation1 15s linear infinite;
}

@keyframes animation1{
  0% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    transform: translateY(-10%) translateZ(0) scaleY(0.9);
  }
  100% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
}

.blob4 {
  z-index: -5;
  overflow: hidden;
  position: absolute;
  overflow: hidden;
  right: 0px;
  top: -10px;
  width: 100%;
  height: 120%;
  transform: rotate(15deg);
  animation: animation2 20s linear infinite;
}

@keyframes animation2{
  0% {
    transform: translateX(0%) translateZ(0) scaleX(1);
  }
  50% {
    transform: translateX(-5%) translateZ(0) scaleX(1.5);
  }
  100% {
    transform: translateX(0%) translateZ(0) scaleX(1);
  }
}

.blob3 {
  z-index: -1;
  overflow: hidden;
  position: absolute;
  overflow: hidden;
  left: 0px;
  bottom: 0px;
  height: 100%;
  width: auto;
  animation: animation3 20s linear infinite;
}

.blob5 {
  z-index: -1;
  overflow: hidden;
  position: absolute;
  overflow: hidden;
  left: -200px;
  bottom: 0px;
  height: 80%;
  width: auto;
  transform: rotate(-20deg);
  animation: animation3 18s linear infinite;
}

@keyframes animation3{
  0% {
    transform: translateX(0) translateZ(0) scaleX(1) scaleY(1);
  }
  50% {
    transform: translateX(-5%) translateY(5%) translateZ(0) scaleX(0.9) scaleY(0.95);
  }
  100% {
    transform: translateX(0) translateZ(0) scaleX(1) scaleY(1);
  }
}